<template>
  <el-main style="margin: 0 100px">
    <div>
      <div style="width: 50%">
        <h1>SCUI</h1>
        <p>
          SCUI
          abi
          
        </p>
        <p>
          SCUIGAS<el-link
            href="https://metamask.io/"
            target="metamask"
            type="primary"
            >MetaMask</el-link
          ><el-link
            href="https://metamask.io/"
            target="metamask"
            type="primary"
            >MetaMask</el-link
          >
        </p>
        <p>
          SCUI<el-link
            href="https://github.com/honghu666/contract-interaction"
            target="contract-interaction"
            type="primary"
            >https://github.com/honghu666/contract-interaction</el-link
          >GitHub
        </p>
        <h1></h1>
        <p>
          SCUI
        </p>
      </div>
      <div style="display: flex; flex-wrap: wrap">
        <div class="address">
          <canvas class="qrcode" ref="ethQrcode"></canvas>
          <div><b>ETH: </b>{{ ethAddress }}</div>
        </div>
        <div class="address">
          <canvas class="qrcode" ref="btcQrcode"></canvas>
          <div><b>BTC: </b>{{ btcAddress }}</div>
        </div>
      </div>
    </div>
  </el-main>
</template>

<script>
import QRious from "qrious";

export default {
  computed: {
    ethAddress: function () {
      return this.configs.ethAddress;
    },
    btcAddress: function () {
      return this.configs.btcAddress;
    },
  },
  mounted: function () {
    new QRious({
      element: this.$refs["ethQrcode"],
      value: this.ethAddress,
      size: 150,
    });
    new QRious({
      element: this.$refs["btcQrcode"],
      value: this.btcAddress,
      size: 150,
    });
  },
};
</script>

<style scoped>
.address {
  text-align: center;
  font-size: 12px;
  margin-right: 20px;
}
p {
  text-align: justify;
}
</style>